import {createContext, useState , useEffect} from "react"
import {storage} from '../storage';

export const FavoritesContext = createContext();

export const FavoritesProvider = ({children}) => {
    const [favorites, setFavorites] = useState(
        storage.exists('favorites') ? storage.getJson('favorites'): []);

    const toggleFavorite = (id) => {
        let newFavorites = [...favorites];
        let index = newFavorites.findIndex((item) => item === id);
        if (index > -1) {
            newFavorites.splice(index, 1); //TODO  splice() /   .  splice()  .
                //TODO splice      
        } else {
            newFavorites.push(id); //TODO  push()     .
        }
        setFavorites(newFavorites); //TODO    
        storage.set('favorites', JSON.stringify(newFavorites)); //TODO     storage 
    };

    const isFavorite = (id) => {
        let index = favorites.findIndex((item) => item === id); //TODO  findIndex     -1 - 
        //console.log(index)
        return index > -1;
    };

useEffect(() => {

    if (storage.exists('favorites')) {
        setFavorites(
            storage.getJson('favorites')
        );
    }
},[]);
    return (
        <FavoritesContext.Provider value={{
            favorites,
            isFavorite,
               toggleFavorite

        }}>
            {children}
        </FavoritesContext.Provider>
    )
};
